/*------------------------------------------------------------------------*\
	INITIAL IMPORTS
\*------------------------------------------------------------------------*/

@import "partials/reset";
@import "partials/variables-mixins";
@import "partials/modules";
//@import "vendor/animate";
//@import "vendor/grids-tiles";

/*------------------------------------------------------------------------*\
	BEGIN
\*------------------------------------------------------------------------*/

html,
body {}

html.sr .load-hidden {
	visibility: hidden;
}

html {
	font-size: $fontSize;

	@include medium-only {
		font-size: $fontSize*0.9;
	}

	@include small {
		font-size: $fontSize*1;
	}

}

body {
	font-size: 1rem;
	background-color: $white;
	color: $mainGrey;
	@extend %font;
	line-height: $lineHeight;
}

p {
	margin-bottom: 1em;
}

h1,
h2,
h3,
h4,
h5 {
	@extend %font-bold;
	margin-bottom: 1rem;
	color: $accentColour;
}

h1 {
	font-size: 1.95rem;
	color: $accentColour;
	.banner & {
		color: $white;
	}
}

h2 {
	font-size: 1.25rem;

}

h3 {
	font-size: 1.25rem;
	margin-bottom: 0;
}

h4 {
	font-size: 1rem;
}

h5 {
	font-size: 1rem;
	margin-bottom: 0;
}

a,
a:link,
a:visited {
	color: $mainGrey;
	transition: color 0.65s;
	text-decoration: underline;
}

a:hover,
a:active {
	color: lighten($mainGrey, 20%);
	text-decoration: underline;
}

img {
	box-sizing: content-box;
	max-width: 100%;
	height: auto;
	border: 0;
	vertical-align: middle;
}

button,
input,
select,
textarea {
	@extend %font;
}


ul {
	@extend %styled-list;
}

strong {
	@extend %font-bold;
}

input,
textarea,
select,
button {
	font-size: 1rem;
}

input,
textarea,
select {
	appearance: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	border-radius: 2px;
	border: none;
	border-bottom: solid 1px $mainGrey;
}

select {
	padding: 0.25rem 3rem 0.25rem 0.5rem;
	background-image: url('images/icon-arrow-down-green.svg');
	background-position: right 0.5rem center;
	background-repeat: no-repeat;
	background-size: 21px 10px;
	background-color: transparent;
}

input {
	background-color: transparent;
	padding: 0.25rem 0.5rem;
	margin-bottom: 1rem;
}

textarea {
	padding: 0.25rem 0.5rem;
}

input[type="submit"] {
	background-color: $white;
	color: $mainGrey;
	// text-transform: uppercase;
	@extend %cursor;
	transition: color $tTime;
	border-radius: 6px;
	padding-top: 0.25rem;
	padding-bottom: 0.25rem;
	padding-left: 1.5rem;
	padding-right: 1.5rem;

	
	padding: 0.25rem 0.5rem;
	

	@extend %font-bold;
	// border: solid 3px $accentColour;
	width: auto;
	transition: $tTime background-color;

	&:hover {
		background-color: $accentColour;
	}
}

input[type="number"] {
	width: 3rem;
}

input[type="checkbox"] {
	appearance: checkbox;
	max-width: none;
	min-width: 0;
}

textarea {
	min-width: 400px;
	min-height: 100px;
	margin-bottom: 1rem;
}

button {
	color: $white;
	border: solid 2px $white;
	background-color: $accentColour;
	// border: none;
	padding: 0.4rem 0.9rem;
	border-radius: 6px;
	@extend %cursor;
	&:hover {
		background-color: rgba($accentColour, 0.5);
		// background-color: $white;
		// color: $accentColour;
		// border-color: $accentColour;
	}
}

table {
	margin-bottom: 1rem;
}

th,
td {
	padding: 10px;

	@include small {
		padding: 5px;
	}

	vertical-align: middle;
}

th:first-child,
td:first-child {
	padding-left: 0;
}

th {
	text-align: left;
	text-transform: uppercase;
}

::selection {
	background: rgba(43, 35, 7, 0.13);
}

::-webkit-input-placeholder {
	color: $midGrey;
}

::-moz-placeholder {
	color: $midGrey;
}

:-ms-input-placeholder {
	color: $midGrey;
}

:-moz-placeholder {
	color: $midGrey;
}

::placeholder {
	color: $midGrey;
}

/*------------------------------------------------------------------------*\
	LAYOUT
\*------------------------------------------------------------------------*/

@import "partials/cols";

.wrap {
	margin: 0 auto;
	max-width: $wrapWidth;
	position: relative;
	padding-left: $gutter;
	padding-right: $gutter;
}

header#header {
	padding-top: $gap;
	@include small {
		padding-top: $gap*0.5;
	}
	padding-bottom: $gap*0.5;
	// position: fixed;
	// top: 0;
	// left: 0;
	// right: 0;
	// width: 100%;
	// z-index: 20;
	background-color: $white;

}

section.section {
	padding-top: $gap;
	padding-bottom: $gap;
}

section.section+section.section {
	padding-top: 0rem;
}

$blackTint: rgba(black, 0.8);
section.section--accent {
	padding-top: $gap;
	padding-bottom: $gap;
	// background-color: $accentColour;
	// background: repeating-linear-gradient(to right, $black 0, $black 10px, transparent 10px, transparent 16px);
	background: linear-gradient(180deg, #17A08F 0%, $accentColour 38% );
}

section.section--grey {
	padding-top: $gap;
	padding-bottom: $gap;
	background-color: $lightGrey;
	// background: repeating-linear-gradient(to right, $black 0, $black 10px, transparent 10px, transparent 16px);
	// background: linear-gradient(180deg, #3C9D93 0%, $accentColour 20%, );
}

.section__title {
	font-size: 1.875rem;
	color: $accentColour;
	text-align: center;
	margin-bottom: $gap;
}

.content-inner {
	max-width: 740px;
	margin-left: auto;
	margin-right: auto;
}

/*------------------------------------------------------------------------*\
	NAV
\*------------------------------------------------------------------------*/

@import "partials/nav-trigger";

.wrap--header-1 {
	padding-left: $gutter;
	padding-right: $gutter;
	max-width: $wrapWidth;
	margin-left: auto;
	margin-right: auto;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

.wrap--header-2 {
	padding-left: $gutter;
	padding-right: $gutter;
	margin-left: auto;
	margin-right: auto;
	max-width: $wrapWidth;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: static;

	body.nav-open & {
		position: static;
	}
}

nav.top-nav {
	text-align: right;
	@extend %font-bold;
	text-transform: uppercase;

	@include small {
		display: none;
		position: absolute;
		top: 135px;
		@include small {
			top: 115px;
		}
		left: 0;
		right: 0;
		bottom: 0;
		height: 100%;
		align-items: center;
		justify-content: center;
		background-color: $white;
		z-index: 10;
		min-height: 25vh;
		// box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.25);
		box-shadow: 0 6px 6px -2px rgba(0, 0, 0, 0.25);
	}

	@include medium {
		display: flex;
	}

	justify-content: space-between;
	align-items: center;

	ul {
		@extend %unstyled-list;
		@include small {
			text-align: center;
		}
	}

	li {
		display: inline-block;

		@include small {
			display: block;
			margin-bottom: 1rem;
		}
		@include medium {
			margin-right: 1rem;
			&:last-child {
				margin-right: 0;
			}
		}


		&.current_page_item {
			&::after {
				margin-top: 0.125rem;
				// margin-bottom: 0.25rem;
				content: '';
				display: block;
				margin-left: auto;
				margin-right: auto;
				width: 0.35rem;
				height: 0.35rem;
				background-color: $accentColour;
				border-radius: 100%;
			}
		}
	}

	a {
		text-decoration: none;
	}
}

.header__rhs {
	flex: 1 1 auto;
	flex-basis: 66.666%;
	max-width: 66.666%;
	text-align: right;
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.login-items {
	font-size: 0.75rem;
	@extend %font-bold;
	@include small {
		display: none;
	}
}

.login-items--mobile {
	font-size: 0.75rem;
	@extend %font-bold;
	margin-top: $gap*0.5;
	@include medium {
		display: none;
	}
}



.login-list {
	@extend %unstyled-list;
	@include medium {
		margin-left: 1rem;
	}
	@include small {
		text-align: center;
	}

	li {
		@include medium {
			display: inline-block;
		}
		margin-left: 0;
		margin-right: 0;
	}
}

.profile-link {
	line-height: 1.4rem;
	vertical-align: middle;
	@include medium {
		margin-right: 0.25rem;
	}

	&::before {
		content: '';
		display: inline-block;
		vertical-align: sub;
		width: 1rem;
		height: 1rem;
		@extend %bg-contain;
		background-image: url('images/icon-profile.svg');
		margin-right: 0.125rem;
	}
}

.logout-link {
	line-height: 1.4rem;
	vertical-align: middle;
	text-decoration: none;
	// margin-right: 0.5rem;
	&::before {
		content: '';
		display: inline-block;
		vertical-align: sub;
		width: 1rem;
		height: 1rem;
		@extend %bg-contain;
		background-image: url('images/icon-logout.svg');
		margin-right: 0.125rem;
	}
}

a.logout-link,
a.profile-link {
	text-decoration: none;
}

nav.functions-nav {
	@extend %font-bold;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: $gap;
	text-transform: uppercase;
	ul {
		text-align: center;
		@extend %unstyled-list;
	}

	li {
		display: inline-block;
		margin-left: 0.5rem;
		margin-right: 0.5rem;

		&.current_page_item {
			&::after {
				margin-top: 0.125rem;
				// margin-bottom: 0.25rem;
				content: '';
				display: block;
				margin-left: auto;
				margin-right: auto;
				width: 0.35rem;
				height: 0.35rem;
				background-color: $accentColour;
				border-radius: 100%;
			}
		}
	}
	a {
		text-decoration: none;
		// padding: 0.4rem 0.9rem;
		// border-radius: 6px;
		// background-color: $accentColour;
		// color: $white;
		// border: solid 1px $accentColour;
	}

	li.current-menu-item {
		// a {
		// 	background-color: $white;
		// 	color: $accentColour;
		// }
	}
}
