}

.site-logo {
	display: block;
	width: 16vw;
	min-width: 200px;
	max-width: 280px;
}

.page-content {
	padding-top: $gap*0.5;
	padding-bottom: $gap*0.5;
	min-height: 900px;
}

.login-items {
	position: absolute;
	top: 1rem;
	right: 1rem;
}

/*------------------------------------------------------------------------*\
	PROPERTY PORTAL
\*------------------------------------------------------------------------*/

a.portal-tile {
	@extend %cursor;
}

a.portal-tile,
.portal-tile {
	z-index: 5;
	display: block;
	background-color: $white;
	color: $mainGrey;
	// padding: $gutter/2;
	// padding-top: $gutter;
	// padding-bottom: $gutter*2;
	box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.25);
	border-radius: 2px;
	position: relative;
	background-repeat: no-repeat;
	background-position: top center;
	background-size: 50%;
	transition: opacity 0.3s;
	margin-bottom: $gutter;
	padding-bottom: 75%;

	.col--1of3 & {
		padding-bottom: 80%;

		@include small {
			padding-bottom: 0;
		}
	}

	@include small {
		padding-bottom: 0;
	}

	position: relative;

	// @include small {
	// 	padding: $gutter*1.5;
	// 	padding-top: $gutter*1.31;
	// }
	&:visited,
	&:link {
		color: $mainGrey;
	}
}

.portal-tile__overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0.2;
	background-color: transparent;
	transition: background-color $tTime;
	a.portal-tile:hover & {
		background-color: $mainGrey;
	}
}

.portal-tile--alt,
a.portal-tile--alt {
	box-shadow: none;

	&::after {
		position: absolute;
		content: '';
		background-color: $mainGrey;

		@include small {
			bottom: 0;
			left: 0;
			right: 0;
			height: 1px;
			width: 80%;
			margin-left: auto;
			margin-right: auto;
		}

		@include medium {
			top: 0;
			right: -($gutter);
			height: 100%;
			width: 1px;
		}
	}

	.col--1of2:nth-child(2n) & {
		&::after {
			@include medium {
				content: none;
			}
		}
	}

	.col--1of3:nth-child(3n) & {
		&::after {
			@include medium {
				content: none;
			}
		}
	}
}

.portal-tile__inner {
	@include small {
		padding-top: $gutter * 1.5;
		padding-bottom: $gutter * 2;
		padding-left: $gutter;
		padding-right: $gutter;
	}

	@include medium {
		position: absolute;
		top: $gutter*1.5;
		left: $gutter;
		right: $gutter;
		bottom: $gutter*2;
	}
}

.portal-tile__footer {
	position: absolute;
	left: $gutter;
	right: $gutter;
	bottom: $gutter*3;
}

.portal-tile__footer {
	text-align: center;
}


a.portal-tile:hover {
	// opacity: 0.6;
}

.portal-tile__title {
	@extend %font-bold;
	margin-bottom: 1rem;
	text-align: center;

	.col--1of2 & {
		@include medium {
			font-size: 1.16rem;
		}
	}
}

.portal-tile__subtitle {
	min-height: 3.5rem;
	margin-bottom: 0.25rem;
}


