	&::before {
		vertical-align: middle;
		margin-right: 0.5rem;
		display: inline-block;
		content: '';
		width: 1rem;
		height: 1rem;
		@extend %bg-contain;
	}
}

.case-handler-details__phone {
	@extend %case-handler-contact;
	&::before {
		background-image: url('images/icon-phone-white.svg');
	}
}

.case-handler-details__email {
	@extend %case-handler-contact;
	&::before {
		background-image: url('images/icon-email-white.svg');
	}
}

.case-handler-details__portrait {
	display: block;
	border-radius: 16px;
	// margin-right: 0.5rem; why?
	padding-bottom: 114.98%;
	@extend %bg-cover;
	background-repeat: no-repeat;
	background-position: center;
}

.case-handler-details__portrait-containter {
	position: relative;
	&::after {
		content: '';
		display: block;
		width: 100%;
		padding-bottom: 42.8%;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		background-image: url('images/portrait-mask.svg');
		@extend %bg-cover;
	}
}

.progress-bar-container {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
}

.progress-bar-text {
	@extend %font-bold;
	font-size: 1.58rem;
	padding-left: 1rem;
}

.progress-bar {
	width: 100%;
	height: 1.1rem;
	background-color: $lightGrey;
	border-radius: 16px;
}

.progress-bar__fill {
	background-color: $accentColour;
	border-radius: 16px;
	height: 100%;
}

.progress-bar__cancelled {
	color: red;
    position: absolute;
    top: 0;
    left: 20%;
    bottom: 0;
    right: 20%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.4rem;
    border-top: solid 2px red;
    border-bottom: solid 2px red;
    transform: rotate(-5deg);
}

.milestone-list {
	@extend %unstyled-list;
