		margin-bottom: 0.5rem;
		&::before {
			content: '';
			width: 1rem;
			height: 1rem;
			@extend %bg-contain;
			margin-right: 1rem;
		}
	}
	li.milestone-status--required {
		&::before {
			background-image: url('images/icon-milestone-required.svg');
		}
	}
	li.milestone-status--completed{
		&::before {
			background-image: url('images/icon-milestone-completed.svg');
		}
	}
}

.tool-tip-source {
