	}
	td:first-child, th:first-child {
		text-align: left;
	}
	a, a:link, a:visited {
		@extend %font-bold;
		color: $accentColour;
	}
	a:hover {
		color: $accentColour;
	}
	thead tr th {
		position: -webkit-sticky; // this is for all Safari (Desktop & iOS), not for Chrome
		position: sticky;
		// top: 145px;
		top: 0;
		z-index: 1; // any positive value, layer order is global
		background: #fff; // any bg-color to overlap
	}
