/*------------------------------------------------------------------------*\
	ACCORDION 
\*------------------------------------------------------------------------*/

.accordion {
	ul {
		margin-bottom: 1rem;
		// color: $white;
	}
	.col--1of2 {
		@include small {
			margin-bottom: 1rem;
		}
	}
}

.accordion__item {
	border-bottom: solid 1px rgba($white,0.25);
}

.accordion__button {
	color: $accentColour;
	background-color: transparent;
	// @extend %large-copy;
	// @extend %font-heading;
	@include small {
		font-size: 1.2rem;
	}
	cursor: pointer;
	width: 100%;
	border: none;
	text-align: left;
	outline: none;
	transition: 0.4s;
	padding: 1rem 0;
	position: relative;
	&::after {
		display: inline-block;
		content: "";
		width: 22px;
		height: 52px;
		text-align: center;
		transition: transform $tTime, color $tTime;
		@extend %bg-cover;
		background-image: url('images/icon-arrow-right-green.svg');
		background-size: 22px 22px;
		background-position: center center;
		position: absolute;
		top: 50%;
		margin-top: -26px;
		right: 0;
	}
	&.active {
		&::after {
			transform: rotate(90deg);
		}
	}
	&:hover {
		background-color: transparent;
	}
}

.accordion__panel {
	max-height: 0;
	overflow: hidden;
	transition: max-height 0.2s ease-out;
}

.accordion__panel__inner {
	padding-top: 0.5rem;
	padding-bottom: 1rem;
}


